import Vue from 'vue';
import App from './App.vue';
import router from './router';

Vue.config.productionTip = false;

// Global Directives
Vue.directive('uppercaseIt', {
  bind(el) {
    const element = el;
    element.style.textTransform = 'uppercase';
  },
});

new Vue({
  router,
  render: h => h(App),
}).$mount('#app');
